<template>
  <div class="app-container">
    <el-container class="header-bg">
      <header-top></header-top>
    </el-container>
    <el-container class="app-slider">
      <!-- <el-aside class="app-side" width='auto'>
        <slider-nav></slider-nav>
      </el-aside> -->
      <el-container class="bg-main">
        <el-header class="header-container">
          <header-nav></header-nav>
        </el-header>
          <el-main class="bg-body">
            <transition name="fade-slide">
              <router-view></router-view>
            </transition>
          </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
// import Vue from 'vue'
// import SliderNav from './slider-nav/SliderNav.vue'
import HeaderNav from './header-nav/HeaderNav.vue'
import HeaderTop from './header-top/HeaderTop.vue'
export default {
  components: {
    // SliderNav,
    HeaderNav,
    HeaderTop
  },
  watch: {
    $route (val) {
      document.querySelector('.bg-body').scrollTop = 0
    }
  }
}
</script>
<style scoped>
.fade-slide-enter-active{
  transition: all .8s;
}
.fade-slide-enter /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateX(50px);
}
.header-container {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 0 3px 0 rgba(0, 0, 0, 0.04);
  /* margin-bottom: 10px; */
  background-color:#fff;
  position: fixed;
  width: 100%;
  z-index: 1000;
  top: 60px;
}
.header-bg {
  background:rgba(117, 172, 230, 1);
  position: fixed;
  z-index: 1000;
  width: 100%;
}
.app-slider {
  height: calc(100vh - 60px)
}
.bg-body{
  /* background-color:#fff; */
  /* height: calc(100vh - 120px); */
  height: 100%;
  overflow-y: auto;
  padding: 0px 10px;
  overflow-x: hidden;
  padding-top: 120px;
  padding-bottom: 80px;
}
.bg-main {
  background-color:#f9f9f9;
  width: 100%;
  height: 100%;
}
.bg-main .ie9{
  padding-left: 210px\9;
}
.bg-main .ie10{
  width: calc(100% - 210px);
}

</style>



