<template>
  <div>
    <el-breadcrumb separator="-" class="breadcrumb" :collapse="isCollapse">
      <el-breadcrumb-item v-for="(item, index) in topTitles" :key="index">
        <a :href="item.path" v-if="isObject(item)" class="link-style">{{item.name}}</a>
        <span v-else>{{item}}</span>
      </el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>

<script>
import tool from '../../event'
import {mapState} from 'vuex'
export default {
  data () {
    return {
      headerTitle: [],
      isCollapse: true,
      title: '',
      preview: ''
    }
  },
  computed: mapState({
    topTitles: state => state.topTitles
  }),
  created () {
    tool.$on('preview', res => {
      this.preview = res
    })
  },
  methods: {
    isObject (val) {
      if (typeof val === 'string') {
        return false
      } else {
        return true
      }
    },
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
.breadcrumb {
  line-height: 60px;
  font-size: 18px;
  font-weight: bold;
}

.toggle {
  float: left;
  margin-top: 12px;
  margin-right: 20px;
  font-size: 30px;
}

.breadcrumb-control {
  float: right;
  display: flex;
  display: -webkit-flex;
}

.breadcrumb-control > li {
  margin: 0 10px;
}
.iconTitle {
  float: left;
}
.goback-btn {
  float: right;
  margin-top: 10px;
  margin-right: 10px;
}
.link-style {
  color: #C12B20;
  border-bottom: 2px solid #C12B20;
  padding-bottom: 1px;
}
</style>



